'use strict'

export default function (router) {
  router.map({
    '*': {
      component: function (resolve) {
        require(['./views/box.vue'], resolve)
      }
    },
    '/': {
      name: 'home',
      component: function (resolve) {
        require(['./views/box.vue'], resolve)
      }
    },
    '/box': {
      name: 'box',
      component: function (resolve) {
        require(['./views/box.vue'], resolve)
      }
    },
    '/rate': {
      name: 'rate',
      component: function (resolve) {
        require(['./views/rate.vue'], resolve)
      }
    },
    '/almanac': {
      name: 'almanac',
      component: function (resolve) {
        require(['./views/almanac.vue'], resolve)
      }
    },
    '/size': {
      name: 'size',
      component: function (resolve) {
        require(['./views/size.vue'], resolve)
      }
    },
    '/unit': {
      name: 'unit',
      component: function (resolve) {
        require(['./views/unit.vue'], resolve)
      }
    },
    '/account': {
      name: 'account',
      component: function (resolve) {
        require(['./views/account.vue'], resolve)
      }
    },
    '/price': {
      name: 'price',
      component: function (resolve) {
        require(['./views/price.vue'], resolve)
      }
    }
  })
}



